class Member {
  constructor(private id: string) { }

  public get member() {
    return this.id
  }

  public set member(id: string) {
    this.id = id
  }
}

class Room {
  constructor(private id: string) { }

  public get room() {
    return this.id
  }

  public set room(id: string) {
    this.id = id
  }
}


const mad = new Member('OH')
console.log(mad.member)
mad.member = 'mad'
console.log(mad.member)

